.social__ok {
  position: relative;

  &::after {
    position: absolute;
    content: "";
    width: 4px;
    height: 4px;
    background-color: $color-tints-alice-blue;
    border-radius: 50px;
    top: 9px;
    left: 13px;
  }

}

.social__pinterest {
  position: relative;
  content: "";
    width: 30px;
    height: 30px;
    background-color: $color-tints-alice-blue;
    border-radius: 50px;

  svg {
    position: absolute;
    z-index: 2;
    background-color: aliceblue;
    border-radius: 50px;
    left: 5px;
    top: 5px;
  }
}
